$grid-gray: #6c757d;
$highlight-blue: #FFD700;
$highlight-light-blue: #cce4ff;


.crossword-grid {
  display: flex;
  flex-direction: column;
  .row {
    justify-content: center;
    > div:first-child {
      border-left: 2px solid black;
    }
    > div:last-child { border-right: 2px solid black; }
  }
  > div:first-of-type {
    .crossword-square {
      border-bottom: none;
      border-top: 2px solid black !important; // !important to handle 1-row case
    }
  }
  > div:last-of-type {
    .crossword-square {
      border-top: 1px solid $grid-gray;
      border-bottom: 2px solid black !important; // !important to handle 1-row case
    }
  }
}

.crossword-square {
  border: solid $grid-gray;
  border-width: 1px 0px 0px 1px;
  display: flex;
  align-items: center;
  input {
    width: 50px;
    height: 50px;
    border: none;
    color: transparent; // hide the cursor
    text-shadow : 0 0 0 #000; // hide the cursor
    &:focus {
      background-color: $highlight-blue !important;
      outline: none;
    }
    text-align: center;
    text-transform: uppercase;
    font-family: arial,sans-serif;
    font-size: 1.7em;
  }
  &.black input { background-color: black }
  &.entry-is-active input { background-color: $highlight-light-blue }
}

.content-container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  text-align: center;
  width: 100%;
  .alert {
    padding-bottom: 20px;
    b {
      font-size: 1.3em;
      font-family: arial,sans-serif;
    }
  }
}
